  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  h3 {
    font-size: 1.1rem;
  }

  p {
    font-size: 16px;
  }

  .container {
    flex-direction: column;
    grid-template-columns: none;
    align-items: center;
    display: flex;
  }

  .container .box {
    max-width: 90%;
    margin-bottom: 20px;
  }

