.about-content {
  width: 50%;
  text-align: center;
  background-color: #fff;
  margin: 3rem auto;
  padding: 4rem;
}

.skills-list {
  text-align: left;
  columns: 2;
  gap: 3rem;
  margin: 2rem 3rem;
  font-size: 1.25rem;
}

.quote {
  padding: 1rem 3rem 0;
}

