  margin: 0;
  font-size: 5rem;
  font-weight: 400;
  line-height: 1;
}

.home-title {
  margin: 1rem 0 0;
  font-size: 2rem;
  font-weight: 400;
}

.header {
  width: 100%;
  z-index: 10;
  background: #ffffffbf;
  justify-content: space-between;
  padding: 1rem;
  display: flex;
  position: fixed;
  top: 0;
}

.hamburger {
  cursor: pointer;
  background: none;
  border: none;
  display: none;
}

.hamburger-line {
  width: 25px;
  height: 3px;
  background: #000;
  margin: 4px 0;
  display: block;
}

.nav {
  gap: 2rem;
  display: flex;
}

@media screen and (width <= 500px) {
  .hamburger {
    display: block;
  }

  .nav {
    width: 100%;
    z-index: 10;
    background: #ffffffe6;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 0;
